/** Icon factory */
export const getIcon = ({ name, props }) => {
  switch (name.replaceAll(' ', '-').toLowerCase()) {
    case 'fastapi':
      return <FastAPIIcon {...props} />
    case 'html':
    case 'html5':
      return <HTML5Icon {...props} />
    case 'css':
    case 'css3':
      return <CSS3Icon {...props} />
    case 'python':
      return <PythonIcon {...props} />
    case 'js':
    case 'javascript':
      return <JavaScriptIcon {...props} />
    case 'postgresql':
      return <PostgreSQLIcon {...props} />
    case 'cpp':
    case 'c++':
      return <CPPIcon {...props} />
    case 'csharp':
    case 'c#':
      return <CSharpIcon {...props} />
    case '.net':
      return <DotNetIcon {...props} />
    case 'aiohttp':
      return <AioHttpIcon {...props} />
    case 'react':
    case 'reactjs':
      return <ReactJSIcon {...props} />
    case 'tkinter':
      return <TkinterIcon {...props} />
    case 'tortoiseorm':
    case 'tortoise-orm':
      return <TortoiseORMIcon {...props} />
    case 'i18n':
      return <GlobeIcon {...props} />
    case 'cdp':
      return <CDPIcon {...props} />
    case 'winapi':
      return <WinAPIIcon {...props} />
    case 'mono':
      return <MonoIcon {...props} />
    case 'asp':
    case 'asp.net':
      return <ASPIcon {...props} />
    case 'django':
      return <DjangoIcon {...props} />
    case 'docker':
      return <DockerIcon {...props} />
    case 'gitlab-ci':
      return <GitlabCIIcon {...props} />
    default:
      return <Icon {...props} />
  }
}
