/** Icon factory */
export const getIcon = ({ name, props }) => {
  switch (name) {
    case 'arduino':
      return <ArduinoIcon {...props} />
    case 'running_man':
      return <RunningManIcon {...props} />
    case 'shield':
      return <ShieldIcon {...props} />
    case 'hiragana':
      return <HiraganaIcon {...props} />
    case 'plane':
      return <PlaneIcon {...props} />
    case 'sega-nes':
      return <SegaNesIcon {...props} />
    case 'note':
      return <NoteIcon {...props} />
    default:
      return <Icon {...props} />
  }
}

